@import '../sass/variables';

.about {
    height: 100vh;
    display: flex;
    flex-direction: column;
    .headings {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-shrink: 0;
        position: relative;
        .headingsWrapper {
            position: absolute;
            top: 287px;
            width: 100%;
            @media screen and(max-width: 1024px) {
                top: 491px;
            }
            @media screen and(max-width: 768px) {
                top: 330px;
            }
            @media (max-width: 414px) {
                top: 177px;
            }
            @media (max-width: 375px) {
                top: 223px;
            }

            .mainHeading {
                font-size: $mainHeadingSize;
                color: $headingColor;
                font-weight: 700;
                text-align: center;
                @media (max-width: 414px) {
                    font-size: 48px;
                }
                @media (max-width: 375px) {
                    font-size: 48px;
                }

            }
            .subHeading {
                font-size: $subHeadingSize;
                color: $headingColor;
                font-weight: 400;
                text-align: center;
                @media (max-width: 414px) {
                    font-size: 26px;
                    margin-top: 10px;
                }
                @media (max-width: 375px) {
                    font-size: 26px;
                    margin-top: 10px;
                }
            }
        }
    }


    .sideMenu {
        flex-grow: 1;
        padding: 0 50px 0 50px;
        position: relative;
        .sideLanguages  {
            position: absolute;
            left: 23px;
            top: 345px;
            width: 70px;
            display: flex;
            align-items: flex-end;
            font-size: 14px;
            color: $textWhiteColor;
            transform: rotate(90deg);
            list-style-type: none;
            @media screen and(max-width: 1024px) {
                top: 500px;
            }
            @media screen and(max-width: 768px) {
                top: 374px;
            }
            @media (max-width: 414px) {
                display: none;
            }
            @media (max-width: 375px) {
                display: none;
            }
            :first-child {
                padding-right: 20px;
            }

        }

        .sidesContacts {
            font-size: $textSize;
            color: $textWhiteColor;

            position: absolute;
            right: 51px;
            top: 198px;
            @media screen and(max-width: 1024px) {
                top: 358px;
            }
            @media screen and(max-width: 768px) {
                top: 230px;
            }
            @media (max-width: 414px) {
                display: none;
            }
            @media (max-width: 375px) {
                display: none;
            }
            img {
                height: 30px;
            }
            .contactElement {
                margin-bottom: 10px;
            }
        }
    }

    @media (max-width: 375px) {
            min-height: 500px;

    }

}

